.Home {
  width: 100%;
  height: 80vh;
  display: grid;
  grid-template-columns: 9% 2fr 1fr;

  align-items: center;
}
.Home .home_itens {
  width: 100%;
  position: relative;
}
.home_itens .logo_horizontal {
  width: 80%;
  pointer-events: none;
  cursor: none;
}
.socialIcones {
  position: relative;
  width: 100%;
}
.socialIcones ul {
  display: grid;
  grid-gap: 16px;
  position: absolute;
  left: 25px;
  top: -24px;
}
.socialIcones img {
  width: 23px;
  transition: 0.1s ease-out;
}
.Home::before {
  content: "";
  width: 4px;
  height: 25%;
  border-radius: 20px;
  background: #fff;
  position: absolute;
  top: 8%;
  left: 32px;
}
.Home::after {
  content: "";
  width: 4px;
  height: 25%;
  border-radius: 20px;
  background: #fff;
  position: absolute;
  top: 63%;
  left: 32px;
}
.entalhe_azul {
  width: 120px;
  position: absolute;
  left: 0px;
  bottom: -300px;
}
.socialIcones img:hover {
  padding: 1px;
}
