.link-nav p {
  padding: 2px 6px;
  font-size: 1em;
}
.link-nav {
  display: inline-block;
  margin: 0px 5px 0px 5px;
  transition: 0.3s ease-out;
}
.nav-bar {
  display: inline-block;
}
header {
  width: 100%;
  height: 50px;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}
.ghost-header {
  width: 30%;
  height: 80%;
}
#bordaazul {
  position: absolute;
  top: 0px;
  right: 0px;
  width: 500px;
  z-index: -1;
}
.link-nav:hover {
  font-size: 17px;
  text-decoration: overline;
  background: #f5f5f527;
  border-radius: 8px;
}
