.celular {
  display: flex;
  justify-content: center;
  width: 100%;
  padding: 30px;
}
.celular_container {
  width: 240px;
  height: 400px;
  border: 5px solid #ffffff;
  border-radius: 25px;
  overflow: hidden;
  position: relative;
  background: url(../lib/earth-11009_1920.png) no-repeat;
  background-size: cover;
  background-position: center;
}
.texto_container {
  position: absolute;
  top: 10%;
  left: 8%;
}
.link_azulado {
  transition: 0.6s ease-out;
  padding: 4px 6px;
  border: 2px solid #67bdbe;
  color: #67bdbe;
  border-radius: 6px;
  position: absolute;
  right: 19%;
  bottom: 42%;
  transition: 0.1s ease-out;
  background: #67bdbe31;
}
.link_azulado:hover {
  border: 2px solid #3f7374;
  color: #043031;
  background: #78e7e9a4;
}
.titulo_um h2 {
  font-size: 1.5em;
  font-weight: 500;
  line-height: 110%;
  padding-bottom: 8px;
}
.legenda_um {
  line-height: 110%;
  font-size: 14px;
  color: #d5d5d5;
}
